html {
  scroll-behavior: smooth;
}

body {
  background-color: var(--bg-color);
  color: var(--content-black-color);
  font-family: Montserrat, sans-serif;
  position: relative;
}

h2, h3, p {
  margin: 0;
}

ul {
  margin: 0;
  padding: 0;
  list-style-type: none;
}

button {
  cursor: pointer;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

a {
  text-decoration: none;
}

:root {
  --content-black-color: #3c322d;
  --content-white-color: #f6f7f7;
  --content-pink-color: #f3848c;
  --bg-color: #e5e2e3;
}

